.fg {
  position: relative;
  margin: 0;
  height: 3.125em;
  input {
    border: 2px solid #EAEEFA;
    padding: 0 .875em;
    border-radius: 0.25em;
    background: #EAEEFA;
    height: 100%;
    width: 100%;
    font-size: 1.125em;
    color: #000;
    &::placeholder {
      color: #474345;
    }
    &:hover{
      background: #DAE1F5;
    }
    &:focus{
      border-color: $primary;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
