.footer{
  background-color: #EBEEF8;
  padding: 3.75em 0;
  color: #898F9F;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__address{
    margin: 1.25em 0 .75em;
    .text14{
      line-height: 160%;
    }
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    margin: .94em 2.5em 0 0;
    span{
      font-size: .875em;
      line-height: 115%;
    }
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__link{
    @include lastM(0 0 1.12em);
    display: flex;
    align-items: center;
    font-weight: 600;
    span{
      font-size: 1.125em;
      line-height: 133%;
    }
    svg{
      width: 2em;
      margin-left: .94em;
    }
    &:hover{
      color: $primary;
      ._rect{
        fill: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.5em 0;
    &__inner{
      display: block;
    }
    &__co{
      font-size: 1em;
    }
    &__address{
      margin-bottom: 0;
    }
  }

  .footer-contacts{
    margin-top: 3em;
    align-items: flex-start;
    &__link{
      font-size: .875em;
      svg{
        order: -1;
        margin: 0 .94em 0 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__item span,
    &__address .text14{
      font-size: .75em;
    }
    &__item{
      margin-top: .75em;
    }
  }
}
