.business{
  background-image: url(../img/fon_2.webp);
  background-size: cover;
  background-position: center;
  &__inner{
    padding: 5.625em 0 10.19em;
    position: relative;
  }
  &__top{
    display: grid;
    grid-template-columns: 34.6875em 34.6875em;
    justify-content: space-between;
    gap: 2em;
  }
  &__breakfast,
  &__lunch{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__lunch{
    @media screen and (min-width: 993px){
      align-items: flex-start;
      .h2{
        color: #fff;
      }
      .business__time{
        clip-path: polygon( 0% 0%,100% 0%,100% 100%,1.449% 100%,0% 0% );
      }
    }
  }
  &__time{
    margin-top: 2.5em;
    clip-path: polygon( 0% 100%,100% 100%,98.53% 0%,0% 0%,0% 100% );
    padding: 0 1.875em;
    height: 3.125em;
    background-color: $yellow;
    display: flex;
    align-items: center;
    white-space: nowrap;
    color: $black;
    text-transform: uppercase;
    span{
      font-size: 1.5em;
      letter-spacing: 0.05em;
    }
  }
  .consult__list{
    li{
      font-weight: 400;
    }
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(3,calc(100% / 3 - 6.25em / 3));
      gap: 3.125em;
      margin-top: 6.25em;
    }
  }
}

.business-form{
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(50%);
  padding: 2.5em 4em;
  background: #FFF;
  width: 100%;
  box-shadow: 0 0 10px rgba(#A9B2CB,.5);
  display: grid;
  grid-template-columns: 16em 12em 17.5em 17.5em calc(100% - 67em);
  gap: 1em;
  &__title{
    font-size: 1.125em;
    line-height: 155%;
    b{
      font-weight: 700;
    }
  }
  &__text{
    position: absolute;
    right: 4em;
    bottom: 1.12em;
    .business-form__hint{
      font-size: .75em;
    }
  }
}

.time-field{
  width: 10.25em;
  height: 3.125em;
  border-radius: 0.3125em;
  display: grid;
  grid-template-columns: repeat(2,50%);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px dashed #EBEEF8;
    border-radius: inherit;
  }
  &__item{
    height: 100%;
    position: relative;
    font-weight: 600;
    input{
      position: absolute;
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
      cursor: pointer;
      &:checked{
        &~label{
          border-color: $yellow;
        }
      }
    }
    label{
      width: 100%;
      height: 100%;
      @include center;
      border-radius: 0.3125rem;
      border: 2px solid transparent;
      transition: all .3s;
    }
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .business{
    background-image: unset;
    &__inner{
      padding: 3em 0;
    }
    &__top{
      grid-template-columns: 100%;
    }
    &__breakfast,
    &__lunch{
      align-items: center;
    }
    &__time{
      font-size: .75em;
      margin-top: 1em;
    }
    .consult__list{
      margin-top: 3em;
    }
  }

  .business-form{
    position: static;
    margin-top: 3em;
    transform: unset;
    padding: 2em 1em;
    grid-template-columns: 100%;
    gap: .75em;
    &__text{
      position: static;
    }
  }
}


