@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.875em;
    border-radius: 0.1875em;
    font-family: $font2;
    font-weight: 700;
    height: 3.125em;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 120%;
        letter-spacing: 0.05625em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: #FFF;
    &:hover{
        background-color: #4B71D6;
        color: #FFF;
    }
    &:active{
        background-color: #294FB4;
        color: #FFF;
    }
}
