.feedback{
  padding: 5.625em 0 6.19em;
  position: relative;
  z-index: 3;
  background-color: #F9FAFD;
  &:before{
    content: '';
    position: absolute;
    bottom: -1em;
    left: 0;
    width: 100%;
    height: 3em;
    opacity: 0.5;
    background: #A9B2CB;
    filter: blur(25px);
    z-index: -1;
  }
  &__inner{
    display: grid;
    grid-template-columns: 49.5625em 27.75em;
    justify-content: space-between;
    gap: 3em;
  }
}

.reviews-slider{
  margin-top: 3.75em;
  border: 3px solid #EFF1F9;
  background: #F9FAFD;
  overflow: hidden;
}

.review-card{
  padding: 3.125em 3.125em 5em;
  min-height: 38em;
  &__name{
    margin-top: 2em;
  }
}

.text{
  p{
    font-size: 1em;
    line-height: 150%;
    @include lastM(0 0 1.5em);
  }
}

.slider{
  position: relative;
}

.swiper-nav{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.81em 1.875em;
}

.swiper-button{
  top: unset;
  left: unset;
  right: unset;
  position: relative;
  margin: 0;
  width: 1.875em;
  svg{
    width: 100%;
  }
}

.swiper-pagination{
  width: auto;
  @include center;
  flex-wrap: wrap;
  margin: 0 1em;
  position: relative;
  left: unset;
  bottom: unset;
  z-index: 3;
  &-bullet{
    background-color: #DBE1EE;
    opacity: 1;
    width: 0.625em;
    height: 0.625em;
    margin: 0 .4375em !important;
    &-active{
      background-color: #FFC12E;
    }
  }
}

.certificates{
  .h2{
    text-align: right;
  }
}

.certificates-slider{
  margin-top: 3.75em;
  .swiper{
    &-slide{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &-active{
        opacity: 1;
        pointer-events: unset;
        filter: unset;
        &~.swiper-slide{

        }
      }
    }
    &-nav{
      z-index: 3;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        //z-index: -1;
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(2.5px);
      }
    }
  }
}

.certificate-card{
  padding: 1.25em;
  background-color: #fff;
  &__img{
    display: block;
    @include imgCover(142%);
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding: 3em 0;
    overflow: hidden;
    &:before{
      display: none;
    }
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .certificates-slider,
  .reviews-slider{
    margin-top: 1.5em;
  }

  .certificates{
    .h2{
      text-align: left;
    }
  }
}


@media screen and (max-width: 576px){
  .review-card{
    padding: 1em 1em 4em;
    min-height: 28em;
    &__name{
      margin-top: 1.25em;
    }
  }

  .swiper-nav{
    padding: 1em;
  }

  .text{
    p{
      font-size: .875em;
      @include lastM(0 0 1em);
    }
  }
}
