.consult{
  padding: 5em 0 4.31em;
  background-image: url(../img/fon_4.webp);
  background-size: cover;
  color: #fff;
  &__content{
    width: 28.125em;
    max-width: 100%;
  }
  &__top{
    position: relative;
  }
  .h2{
    line-height: 125%;
  }
  &__price{
    clip-path: polygon( 0% 0%,100% 0%,96.859% 100%,0% 100%,0% 0% );
    position: absolute;
    bottom: .44em;
    left: 22.5em;
    padding: 0 1.25em;
    height: 2.5625em;
    background-color: $yellow;
    display: flex;
    align-items: center;
    white-space: nowrap;
    color: $black;
    text-transform: uppercase;
    span{
      font-size: 1.5em;
      letter-spacing: 0.05em;
    }
  }
  &__list{
    margin-top: 2em;
    @include grid100gap(1.5em);
    li{
      font-weight: 700;
      display: flex;
      align-items: flex-start;
      img{
        width: .8125em;
        flex-shrink: 0;
        margin-right: 1em;
        margin-top: .125em;
      }
      span{
        font-size: 1em;
        line-height: 137%;
      }
    }
  }
}

.business-form,
.consult-form{
  &__hint{
    font-size: .8125em;
    line-height: 123%;
    color: #A9B2CB;
    a{
      color: $primary;
      &:hover{
        color: #294FB4;
        text-decoration: underline !important;
      }
    }
  }
}

.consult-form{
  padding: 2.5em 3.75em;
  background-color: #fff;
  margin-top: 3em;
  @include grid100gap(1.25em);
  &__btn{
    margin-top: .625em;
  }
}

.meeting{
  padding: 9.375em 0 4.85em;
  background-color: #F9FAFD;
  .h2{
    text-align: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 6.5em / 3));
    gap: 3.25em;
    margin-top: 3.75em;
  }
}

.meeting-item{
  display: flex;
  align-items: center;
  &__icon{
    background: #FFF;
    box-shadow: 0px 10px 50px 0px rgba(169, 178, 203, 0.25);
    width: 5.5em;
    height: 5.5em;
    flex-shrink: 0;
    @include center;
    margin-right: 1.875em;
    img{
      width: 3em;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 150%;
    b{
      font-weight: 700;
    }
  }
}

.questions{
  margin-top: 5.94em;
  column-count: 2;
  column-gap: 3.81em;
}

.question-item{
  @include lastM(0 0 1.71em);
  display: inline-flex;
  width: 100%;
  padding-left: 7.375em;
  position: relative;
  &__num{
    position: absolute;
    top: 0;
    left: 0;
    font-size: 6.25em;
    line-height: 80%;
    opacity: 0.2;
    color: #A9B2CB;
    font-family: $font2;
    font-weight: 200;
  }
  &__title{
    font-size: 1.5em;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding: 2em 0;
    &__inner{
      padding: 2em 0;
    }
  }

  .meeting{
    padding: 2em 0;
    .h2{
      text-align: left;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
      margin-top: 2em;
    }
  }

  .meeting-item{
    &__icon{
      width: 3.5em;
      height: 3.5em;
      margin-right: 1em;
      img{
        width: 2em;
      }
    }
    &__text{
      font-size: .875em;
    }
  }

  .questions{
    column-count: 1;
    margin-top: 3em;
  }

  .question-item{
    padding-left: 5em;
  }
}


@media screen and (max-width: 576px){
  .consult{
    &__top{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__price{
      position: static;
      margin-top: 1em;
    }
    &__list{
      font-size: .875em;
      gap: 1em;
    }
  }

  .consult-form{
    padding: 2em 1em;
    gap: .75em;
    margin-top: 2em;
  }

  .meeting{
    .h2{
      max-width: 90%;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .question-item{
    font-size: .75em;
    padding-left: 4.5em;
    &__desc{
      margin-top: .5em;
    }
  }
}
