.main{
  background-image: url(../img/bg.svg);
  background-size: cover;
  &__inner{
    padding: 12.56em 0 10.69em;
    position: relative;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: -1.875em;
    width: 52.9375em;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    width: 42.5em;
    max-width: 100%;
  }
  &__subtitle{
    padding-bottom: 2.75em;
    position: relative;
    margin-bottom: 2.5em;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 9.375em;
      height: 0.1875em;
      max-width: 100%;
      background: linear-gradient(90deg, #FFC12E 0%, rgba(255, 193, 46, 0.00) 100%);
    }
  }
  &__title{
    color: $dark;
    font-size: 3.75em;
    line-height: 107%;
    letter-spacing: 0.05em;
    font-weight: 500;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__desc{
    margin-top: 2.5em;
    max-width: 34em;
  }
  &__bottom{
    margin-top: 4.375em;
    display: flex;
    align-items: center;
  }
  &__btn{
    flex-shrink: 0;
    margin-right: 2.19em;
  }
  &__desc2{
    span{
      background-color: $yellow;
      padding: 0 .25em;
      font-weight: 700;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 7em 0 0;
    }
    &__content{
      width: 100%;
    }
    &__img{
      position: static;
      width: 100%;
      max-width: 30em;
      height: auto;
      margin: 2em auto 0;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__subtitle{
      max-width: 15em;
      padding-bottom: 1em;
      margin-bottom: 1.5em;
    }
    &__title{
      font-size: 2em;
      letter-spacing: unset;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__bottom{
      flex-direction: column;
      margin-top: 2em;
    }
    &__btn{
      width: 100%;
      margin: 0 0 1.25em;
    }
  }
}
