.header{
  padding: 2.5em 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  &__link{
    margin-right: 2.5em;
    color: #474345;
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
}

.lang{
  display: flex;
  align-items: center;
  &__icon{
    width: 1.375em;
    margin-right: 1.25em;
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    color: #474345;
    @include lastM(0 .94em 0 0);
    &._active{
      opacity: .25;
    }
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &._open{
      .nav{
        transform: translateX(0);
      }
      .burger{
        ._view2{
          opacity: 1;
        }
        ._view1{
          opacity: 0;
        }
      }
    }
  }

  .nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 9;
    padding: 5em 1.125em 1em;
    flex-direction: column;
    align-items: flex-start;
    transition: all .5s;
    transform: translateX(-120%);
    &__link{
      margin: 0 0 1.25em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .burger{
    background-color: transparent;
    @include center;
    position: relative;
    z-index: 10;
    width: 1.125em;
    svg{
      @include centerAbsolute;
    }
    ._view1{
      width: 1.125em;
    }
    ._view2{
      width: .75em;
      opacity: 0;
      pointer-events: none;
    }
  }
}


